"use client"

import type React from "react"
import { LayoutWrapper } from "@/components/layout-wrapper"
import { Search, Plus, Edit2, X, Paperclip } from "lucide-react"
import { useState, useEffect, useRef } from "react"
import { getCurrentUser, checkPermission } from "@/lib/constants"
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from "@/components/ui/alert-dialog"
import { Calendar } from "@/components/ui/calendar"
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import { Button } from "@/components/ui/button"
import { useToast } from "@/hooks/use-toast"
import { CalendarIcon } from "lucide-react"
import { format } from "date-fns"

type PatientRole = "student" | "teaching_staff" | "non_teaching_staff"
type Program = "CICT" | "CBME"
type StudentCourse = "BSIT" | "BSCS" | "BSIS" | "BTVTED"
type CBMECourse = "BSA" | "BSAIS" | "BPA" | "BSE"
type NonTeachingCategory = "Administration" | "Accounting" | "Human Resources" | "Student Service" | "Library" | "Maintenance" | "Security" | "Supply" | "Clinic"

interface Patient {
  id: string
  firstName: string
  middleName?: string
  lastName: string
  suffix?: string
  dateOfBirth: string
  gender: "Male" | "Female"
  phone: string
  email?: string
  address?: string
  role: PatientRole
  idNumber: string
  program?: Program
  course?: StudentCourse | CBMECourse
  yearLevel?: number
  block?: number
  department?: Program
  staffCategory?: NonTeachingCategory
  pastIllnesses?: string
  surgeries?: string
  currentMedication?: string
  allergies?: string
  medicalNotes?: string
  primaryContactName?: string
  primaryContactRelationship?: string
  primaryContactPhone?: string
  primaryContactAddress?: string
  secondaryContactName?: string
  secondaryContactRelationship?: string
  secondaryContactPhone?: string
  secondaryContactAddress?: string
  attachments?: string
}

interface FormData {
  firstName: string
  middleName: string
  lastName: string
  suffix: string
  dateOfBirth: string
  gender: "Male" | "Female"
  phone: string
  email: string
  address: string
  role: PatientRole
  idNumber: string
  program: Program | ""
  course: string
  yearLevel: number | ""
  block: number | ""
  department: Program | ""
  staffCategory: NonTeachingCategory | ""
  pastIllnesses: string
  surgeries: string
  currentMedication: string
  allergies: string
  medicalNotes: string
  primaryContactName: string
  primaryContactRelationship: string
  primaryContactPhone: string
  primaryContactAddress: string
  secondaryContactName: string
  secondaryContactRelationship: string
  secondaryContactPhone: string
  secondaryContactAddress: string
  attachments?: string
}

const COURSE_OPTIONS: Record<Program, string[]> = {
  CICT: ["BSIT", "BSCS", "BSIS", "BTVTED"],
  CBME: ["BSA", "BSAIS", "BPA", "BSE"],
}

export default function PatientsPage() {
  const [patients, setPatients] = useState<Patient[]>([])
  const [searchTerm, setSearchTerm] = useState("")
  const [roleFilter, setRoleFilter] = useState("all")
  const [sortOption, setSortOption] = useState("name_az")
  const [showForm, setShowForm] = useState(false)
  const [editingPatient, setEditingPatient] = useState<Patient | null>(null)
  const initialFormData: FormData = {
    firstName: "",
    middleName: "",
    lastName: "",
    suffix: "",
    dateOfBirth: "",
    gender: "Male",
    phone: "",
    email: "",
    address: "",
    role: "student",
    idNumber: "",
    program: "",
    course: "",
    yearLevel: "",
    block: "",
    department: "",
    staffCategory: "",
    pastIllnesses: "",
    surgeries: "",
    currentMedication: "",
    allergies: "",
    medicalNotes: "",
    primaryContactName: "",
    primaryContactRelationship: "",
    primaryContactPhone: "",
    primaryContactAddress: "",
    secondaryContactName: "",
    secondaryContactRelationship: "",
    secondaryContactPhone: "",
    secondaryContactAddress: "",
    attachments: "",
  }

  const [formData, setFormData] = useState<FormData>(initialFormData)
  const [availableCourses, setAvailableCourses] = useState<string[]>([])
  const [expandedId, setExpandedId] = useState<string | null>(null)
  const [user, setUser] = useState<any>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [selectedDateOfBirth, setSelectedDateOfBirth] = useState<Date | undefined>()
  const [formErrors, setFormErrors] = useState<Record<string, boolean>>({})
  const { toast } = useToast()

  // Refs for form fields
  const firstNameRef = useRef<HTMLInputElement>(null)
  const lastNameRef = useRef<HTMLInputElement>(null)
  const dateOfBirthRef = useRef<HTMLDivElement>(null)
  const genderRef = useRef<HTMLSelectElement>(null)
  const phoneRef = useRef<HTMLInputElement>(null)
  const programRef = useRef<HTMLSelectElement>(null)
  const courseRef = useRef<HTMLSelectElement>(null)
  const yearLevelRef = useRef<HTMLSelectElement>(null)
  const departmentRef = useRef<HTMLSelectElement>(null)
  const staffCategoryRef = useRef<HTMLSelectElement>(null)
  const primaryContactNameRef = useRef<HTMLInputElement>(null)
  const primaryContactRelationshipRef = useRef<HTMLInputElement>(null)
  const primaryContactPhoneRef = useRef<HTMLInputElement>(null)

  useEffect(() => {
    const currentUser = getCurrentUser()
    if (currentUser) setUser(currentUser)
  }, [])

  useEffect(() => {
    if (user) fetchPatients()
  }, [user])

  useEffect(() => {
    if (formData.program && formData.program in COURSE_OPTIONS) {
      setAvailableCourses(COURSE_OPTIONS[formData.program])
      if (!COURSE_OPTIONS[formData.program].includes(formData.course)) {
        setFormData((prev) => ({ ...prev, course: "" }))
      }
    } else {
      setAvailableCourses([])
    }
  }, [formData.program])

  const fetchPatients = async () => {
    if (!user) return
    try {
      setIsLoading(true)
      const response = await fetch("/api/patients", { headers: { "x-user-id": user.id } })
      if (response.ok) {
        const data = await response.json()
        setPatients(data.patients || [])
      }
    } catch (error) {
      console.error(error)
    } finally {
      setIsLoading(false)
    }
  }

  const canManagePatients = user && checkPermission(user.role, "canManagePatients")
  const canEditPatients = user && checkPermission(user.role, "canEditPatients")

  const createActivity = async (type: string, message: string) => {
    try {
      await fetch("/api/activities", {
        method: "POST",
        headers: { "Content-Type": "application/json", "x-user-id": user.id },
        body: JSON.stringify({ type, message }),
      })
    } catch (error) {
      console.error("Failed to create activity:", error)
    }
  }

  const getFullName = (p: Patient) =>
    `${p.firstName} ${p.middleName ? p.middleName + " " : ""}${p.lastName}${p.suffix ? " " + p.suffix : ""}`

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target

    // Name validation - only letters and spaces allowed
    if (['firstName', 'middleName', 'lastName', 'suffix'].includes(name)) {
      if (!/^[a-zA-Z\s]*$/.test(value)) {
        return // Don't update if invalid characters
      }
    }

    // Phone validation - only numbers, max 11 digits, must start with 09
    if (name === 'phone') {
      if (!/^09\d{0,9}$/.test(value)) {
        return // Don't update if invalid
      }
    }

    setFormData((prev) => ({ ...prev, [name]: value }))

    // Clear error when user starts typing
    if (formErrors[name]) {
      setFormErrors((prev) => ({ ...prev, [name]: false }))
    }
  }

  const validateForm = () => {
    const errors: Record<string, boolean> = {}

    // Personal Info
    if (!formData.firstName.trim() || !/^[a-zA-Z\s]+$/.test(formData.firstName.trim())) {
      errors.firstName = true
    }
    if (!formData.lastName.trim() || !/^[a-zA-Z\s]+$/.test(formData.lastName.trim())) {
      errors.lastName = true
    }
    if (!selectedDateOfBirth) {
      errors.dateOfBirth = true
    }
    if (!formData.gender) {
      errors.gender = true
    }
    if (!formData.phone.trim() || !/^09\d{9}$/.test(formData.phone.trim())) {
      errors.phone = true
    }

    // Role-specific
    if (formData.role === "student") {
      if (!formData.program) {
        errors.program = true
      }
      if (!formData.course) {
        errors.course = true
      }
      if (!formData.yearLevel) {
        errors.yearLevel = true
      }
    } else if (formData.role === "teaching_staff") {
      if (!formData.department) {
        errors.department = true
      }
    } else if (formData.role === "non_teaching_staff") {
      if (!formData.staffCategory) {
        errors.staffCategory = true
      }
    }

    // Emergency Contacts
    if (!formData.primaryContactName.trim()) {
      errors.primaryContactName = true
    }
    if (!formData.primaryContactRelationship.trim()) {
      errors.primaryContactRelationship = true
    }
    if (!formData.primaryContactPhone.trim() || !/^09\d{9}$/.test(formData.primaryContactPhone.trim())) {
      errors.primaryContactPhone = true
    }

    setFormErrors(errors)

    // Scroll to first error
    const errorFields = [
      'firstName', 'lastName', 'dateOfBirth', 'gender', 'phone',
      'program', 'course', 'yearLevel', 'department', 'staffCategory',
      'primaryContactName', 'primaryContactRelationship', 'primaryContactPhone'
    ]

    for (const field of errorFields) {
      if (errors[field]) {
        const refMap: Record<string, React.RefObject<any>> = {
          firstName: firstNameRef,
          lastName: lastNameRef,
          dateOfBirth: dateOfBirthRef,
          gender: genderRef,
          phone: phoneRef,
          program: programRef,
          course: courseRef,
          yearLevel: yearLevelRef,
          department: departmentRef,
          staffCategory: staffCategoryRef,
          primaryContactName: primaryContactNameRef,
          primaryContactRelationship: primaryContactRelationshipRef,
          primaryContactPhone: primaryContactPhoneRef,
        }
        refMap[field]?.current?.scrollIntoView({ behavior: 'smooth', block: 'center' })
        break
      }
    }

    return Object.keys(errors).length === 0
  }

  const handleAddOrEdit = async () => {
    if (!validateForm()) {
      return // Stop if validation fails
    }

    try {
      const method = editingPatient ? "PUT" : "POST"
      const url = "/api/patients"
      const body = editingPatient ? { ...formData, id: editingPatient.id } : formData
      const response = await fetch(url, {
        method,
        headers: { "Content-Type": "application/json", "x-user-id": user.id },
        body: JSON.stringify(body),
      })
      if (response.ok) {
        const patientName = `${formData.firstName} ${formData.lastName}`
        const action = editingPatient ? "updated" : "added"
        await createActivity("patient", `Patient ${patientName} was ${action}`)
        toast({
          title: "Success",
          description: `Patient ${patientName} has been ${action} successfully.`,
        })
        if (!editingPatient) {
          // Reset form data only when adding a new patient, keep form open
          setFormData(initialFormData)
          setSelectedDateOfBirth(undefined)
          setAvailableCourses([])
          setFormErrors({})
        } else {
          // Close form after editing
          setShowForm(false)
          setEditingPatient(null)
        }
        fetchPatients()
      } else {
        const errorData = await response.json().catch(() => ({ error: 'Unknown error' }))
        console.error("Failed to save patient:", response.status, errorData)
        toast({
          title: "Error",
          description: `Failed to save patient: ${errorData.error || errorData.errors?.join(', ') || 'Unknown error'}`,
          variant: "destructive",
        })
      }
    } catch (error) {
      console.error(error)
    }
  }

  const handleEditClick = (patient: Patient) => {
    setEditingPatient(patient)
    setFormData({
      firstName: patient.firstName,
      middleName: patient.middleName || "",
      lastName: patient.lastName,
      suffix: patient.suffix || "",
      dateOfBirth: patient.dateOfBirth,
      gender: patient.gender,
      phone: patient.phone,
      email: patient.email || "",
      address: patient.address || "",
      role: patient.role,
      idNumber: patient.idNumber,
      program: patient.program || "CICT",
      course: patient.course || "",
      yearLevel: patient.yearLevel || "",
      block: patient.block || "",
      department: patient.department || "",
      staffCategory: patient.staffCategory || "",
      pastIllnesses: patient.pastIllnesses || "",
      surgeries: patient.surgeries || "",
      currentMedication: patient.currentMedication || "",
      allergies: patient.allergies || "",
      medicalNotes: patient.medicalNotes || "",
      primaryContactName: patient.primaryContactName || "",
      primaryContactRelationship: patient.primaryContactRelationship || "",
      primaryContactPhone: patient.primaryContactPhone || "",
      primaryContactAddress: patient.primaryContactAddress || "",
      secondaryContactName: patient.secondaryContactName || "",
      secondaryContactRelationship: patient.secondaryContactRelationship || "",
      secondaryContactPhone: patient.secondaryContactPhone || "",
      secondaryContactAddress: patient.secondaryContactAddress || "",
      attachments: patient.attachments || "",
    })
    setSelectedDateOfBirth(new Date(patient.dateOfBirth))
    setAvailableCourses(patient.program ? COURSE_OPTIONS[patient.program] : [])
    setShowForm(true)
  }

  const handleDelete = async (id: string) => {
    try {
      const patient = patients.find(p => p.id === id)
      const response = await fetch("/api/patients", {
        method: "DELETE",
        headers: { "Content-Type": "application/json", "x-user-id": user.id },
        body: JSON.stringify({ id }),
      })

      if (response.ok) {
        if (patient) {
          const patientName = `${patient.firstName} ${patient.lastName}`
          await createActivity("patient", `Patient ${patientName} was deleted`)
          toast({
            title: "Success",
            description: `Patient ${patientName} has been deleted successfully.`,
          })
        }
        fetchPatients()
      } else {
        const errorData = await response.json()
        toast({
          title: "Error",
          description: `Failed to delete patient: ${errorData.error || 'Unknown error'}`,
          variant: "destructive",
        })
      }
    } catch (error) {
      console.error("Delete error:", error)
      toast({
        title: "Error",
        description: "An error occurred while deleting the patient. Please try again.",
        variant: "destructive",
      })
    }
  }

  const filteredPatients = patients
    .filter((p) =>
      getFullName(p).toLowerCase().includes(searchTerm.toLowerCase()) ||
      p.idNumber.toLowerCase().includes(searchTerm.toLowerCase())
    )
    .filter((p) => roleFilter === "all" || p.role === roleFilter)
    .sort((a, b) => {
      if (sortOption === "name_az") return getFullName(a).localeCompare(getFullName(b))
      if (sortOption === "name_za") return getFullName(b).localeCompare(getFullName(a))
      if (sortOption === "id_asc") return a.idNumber.localeCompare(b.idNumber)
      if (sortOption === "id_desc") return b.idNumber.localeCompare(a.idNumber)
      return 0
    })

  return (
    <LayoutWrapper>
      <div className="space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-[#8B3A3A]">Patients</h1>
            <p className="text-gray-600">Manage school community member records</p>
          </div>
          {canManagePatients && (
            <button
              onClick={() => { setShowForm(true); setEditingPatient(null); setFormData(initialFormData); setSelectedDateOfBirth(undefined); setAvailableCourses([]); setFormErrors({}); }}
              className="bg-[#8B3A3A] text-white px-6 py-3 rounded-lg font-semibold hover:bg-[#6D2E2E] flex items-center gap-2"
            >
              <Plus size={20} /> Add Patient
            </button>
          )}
        </div>

        {/* Search & Filters */}
        <div className="bg-[#8B3A3A] rounded-xl p-6">
          <div className="flex items-center gap-4">
            <div className="flex-1 relative">
              <Search className="absolute left-4 top-3 text-white opacity-60" size={20} />
              <input
                type="text"
                placeholder="Search by name or ID..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-12 pr-4 py-2 bg-white/20 text-white placeholder-white/60 rounded-lg focus:outline-none focus:ring-2 focus:ring-white"
              />
            </div>
            <select
              value={roleFilter}
              onChange={(e) => setRoleFilter(e.target.value)}
              className="bg-white text-[#8B3A3A] px-3 py-2 rounded-lg focus:outline-none focus:ring-2 focus:ring-white font-medium cursor-pointer text-sm"
            >
              <option value="all">All</option>
              <option value="student">Student</option>
              <option value="teaching_staff">Teaching Staff</option>
              <option value="non_teaching_staff">Non-Teaching Staff</option>
            </select>
            <select
              value={sortOption}
              onChange={(e) => setSortOption(e.target.value)}
              className="bg-white text-[#8B3A3A] px-3 py-2 rounded-lg focus:outline-none focus:ring-2 focus:ring-white font-medium cursor-pointer text-sm"
            >
              <option value="name_az">Name: A-Z</option>
              <option value="name_za">Name: Z-A</option>
              <option value="id_asc">ID: Asc</option>
              <option value="id_desc">ID: Desc</option>
            </select>
          </div>
        </div>

        {/* Add/Edit Form */}
        {showForm && canManagePatients && (
          <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 relative">
            <button
              onClick={() => { setShowForm(false); setEditingPatient(null); setSelectedDateOfBirth(undefined); setFormErrors({}); }}
              className="absolute top-4 right-4 p-2 hover:bg-gray-100 rounded-full transition-colors"
              title="Close form"
            >
              <X size={20} />
            </button>
            <h2 className="text-xl font-semibold mb-4">{editingPatient ? "Edit Patient" : "Add Patient"}</h2>

            {/* FORM START */}
            <div className="space-y-6">
              {/* Personal Info */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div><label>First Name <span className="text-red-500">*</span></label><input ref={firstNameRef} type="text" name="firstName" value={formData.firstName} onChange={handleInputChange} placeholder="Enter first name" className={`border rounded px-3 py-2 w-full ${formErrors.firstName ? 'border-red-500' : ''}`} required/></div>
                <div><label>Middle Name</label><input type="text" name="middleName" value={formData.middleName} onChange={handleInputChange} placeholder="Enter middle name" className="border rounded px-3 py-2 w-full"/></div>
                <div><label>Last Name <span className="text-red-500">*</span></label><input ref={lastNameRef} type="text" name="lastName" value={formData.lastName} onChange={handleInputChange} placeholder="Enter last name" className={`border rounded px-3 py-2 w-full ${formErrors.lastName ? 'border-red-500' : ''}`} required/></div>
                <div><label>Suffix</label><input type="text" name="suffix" value={formData.suffix} onChange={handleInputChange} placeholder="e.g. Jr., Sr." className="border rounded px-3 py-2 w-full"/></div>
                <div ref={dateOfBirthRef}>
                  <label>Date of Birth <span className="text-red-500">*</span></label>
                  <Popover>
                    <PopoverTrigger asChild>
                      <Button
                        variant="outline"
                        className={`w-full justify-start text-left font-normal ${!selectedDateOfBirth && "text-muted-foreground"} ${formErrors.dateOfBirth ? 'border-red-500' : ''}`}
                      >
                        <CalendarIcon className="mr-2 h-4 w-4" />
                        {selectedDateOfBirth ? format(selectedDateOfBirth, "MM/dd/yyyy") : "Select date"}
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0" side="bottom" align="start">
                      <Calendar
                        mode="single"
                        selected={selectedDateOfBirth}
                        onSelect={(date) => {
                          setSelectedDateOfBirth(date)
                          if (date) {
                            setFormData((prev) => ({ ...prev, dateOfBirth: format(date, "MM/dd/yyyy") }))
                            setFormErrors((prev) => ({ ...prev, dateOfBirth: false }))
                          }
                        }}
                        disabled={(date) => date > new Date()}
                        fromYear={1900}
                        toYear={new Date().getFullYear()}
                        captionLayout="dropdown"
                        initialFocus
                      />
                    </PopoverContent>
                  </Popover>
                </div>
                <div>
                  <label>Gender <span className="text-red-500">*</span></label>
                  <select ref={genderRef} name="gender" value={formData.gender} onChange={handleInputChange} className={`border rounded px-3 py-2 w-full ${formErrors.gender ? 'border-red-500' : ''}`} required>
                    <option value="Male">Male</option>
                    <option value="Female">Female</option>
                  </select>
                </div>
                <div><label>Phone <span className="text-red-500">*</span></label><input ref={phoneRef} type="text" name="phone" value={formData.phone} onChange={handleInputChange} placeholder="09xxxxxxxxx" className={`border rounded px-3 py-2 w-full ${formErrors.phone ? 'border-red-500' : ''}`} maxLength={11} required/></div>
                <div><label>Email</label><input type="email" name="email" value={formData.email} onChange={handleInputChange} placeholder="Enter email address" className="border rounded px-3 py-2 w-full"/></div>
                <div className="md:col-span-2"><label>Address</label><input type="text" name="address" value={formData.address} onChange={handleInputChange} placeholder="Enter full address" className="border rounded px-3 py-2 w-full"/></div>
              </div>

              {/* School / Role Details */}
              <div className="border-t pt-4 space-y-4">
                <h3 className="font-semibold">School / Role Details</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div><label>Role</label>
                    <select name="role" value={formData.role} onChange={handleInputChange} className="border rounded px-3 py-2 w-full">
                      <option value="student">Student</option>
                      <option value="teaching_staff">Teaching Staff</option>
                      <option value="non_teaching_staff">Non-Teaching Staff</option>
                    </select>
                  </div>
                  <div><label>ID Number</label><input type="text" name="idNumber" value={formData.idNumber} onChange={handleInputChange} placeholder="Enter ID number" className="border rounded px-3 py-2 w-full"/></div>
                  {formData.role === "student" && <>
                    <div><label>Program <span className="text-red-500">*</span></label>
                      <select ref={programRef} name="program" value={formData.program} onChange={handleInputChange} className={`border rounded px-3 py-2 w-full ${formErrors.program ? 'border-red-500' : ''}`} required>
                        <option value="">Select program</option>
                        <option value="CICT">CICT</option>
                        <option value="CBME">CBME</option>
                      </select>
                    </div>
                    <div><label>Course <span className="text-red-500">*</span></label>
                      <select ref={courseRef} name="course" value={formData.course} onChange={handleInputChange} className={`border rounded px-3 py-2 w-full ${formErrors.course ? 'border-red-500' : ''}`} required>
                        <option value="">Select course</option>
                        {availableCourses.map(c => <option key={c} value={c}>{c}</option>)}
                      </select>
                    </div>
                    <div>
                      <label>Year Level <span className="text-red-500">*</span></label>
                      <select ref={yearLevelRef} name="yearLevel" value={formData.yearLevel} onChange={handleInputChange} className={`border rounded px-3 py-2 w-full ${formErrors.yearLevel ? 'border-red-500' : ''}`} required>
                        <option value="">Select year level</option>
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                      </select>
                    </div>
                    <div>
                      <label>Block</label>
                      <select name="block" value={formData.block} onChange={handleInputChange} className="border rounded px-3 py-2 w-full">
                        <option value="">Select block</option>
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                        <option value="5">5</option>
                      </select>
                    </div>
                  </>}
                  {formData.role !== "student" && <>
                    {formData.role === "teaching_staff" && <div><label>Department <span className="text-red-500">*</span></label>
                      <select ref={departmentRef} name="department" value={formData.department} onChange={handleInputChange} className={`border rounded px-3 py-2 w-full ${formErrors.department ? 'border-red-500' : ''}`} required>
                        <option value="">Select department</option>
                        <option value="CICT">CICT</option>
                        <option value="CBME">CBME</option>
                      </select>
                    </div>}
                    {formData.role === "non_teaching_staff" && <div><label>Staff Category <span className="text-red-500">*</span></label>
                      <select ref={staffCategoryRef} name="staffCategory" value={formData.staffCategory} onChange={handleInputChange} className={`border rounded px-3 py-2 w-full ${formErrors.staffCategory ? 'border-red-500' : ''}`} required>
                        <option value="">Select staff category</option>
                        <option value="Administration">Administration</option>
                        <option value="Accounting">Accounting</option>
                        <option value="Human Resources">Human Resources</option>
                        <option value="Student Service">Student Service</option>
                        <option value="Library">Library</option>
                        <option value="Maintenance">Maintenance</option>
                        <option value="Security">Security</option>
                        <option value="Supply">Supply</option>
                        <option value="Clinic">Clinic</option>
                      </select>
                    </div>}
                  </>}
                </div>
              </div>

              {/* Medical Records */}
              <div className="border-t pt-4 space-y-4">
                <h3 className="font-semibold">Medical Records</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div><label>Past Illnesses</label><textarea name="pastIllnesses" value={formData.pastIllnesses} onChange={handleInputChange} placeholder="List any past illnesses" className="border rounded px-3 py-2 w-full"/></div>
                  <div><label>Surgeries</label><textarea name="surgeries" value={formData.surgeries} onChange={handleInputChange} placeholder="List any surgeries" className="border rounded px-3 py-2 w-full"/></div>
                  <div><label>Current Medication</label><textarea name="currentMedication" value={formData.currentMedication} onChange={handleInputChange} placeholder="List current medications" className="border rounded px-3 py-2 w-full"/></div>
                  <div><label>Allergies</label><textarea name="allergies" value={formData.allergies} onChange={handleInputChange} placeholder="List any allergies" className="border rounded px-3 py-2 w-full"/></div>
                  <div className="md:col-span-2"><label>Medical Notes</label><textarea name="medicalNotes" value={formData.medicalNotes} onChange={handleInputChange} placeholder="Additional medical notes" className="border rounded px-3 py-2 w-full"/></div>
                </div>
              </div>

              {/* Emergency Contacts */}
              <div className="border-t pt-4 space-y-4">
                <h3 className="font-semibold">Emergency Contacts</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div><label>Primary Contact Name <span className="text-red-500">*</span></label><input ref={primaryContactNameRef} type="text" name="primaryContactName" value={formData.primaryContactName} onChange={handleInputChange} className={`border rounded px-3 py-2 w-full ${formErrors.primaryContactName ? 'border-red-500' : ''}`} required/></div>
                  <div><label>Relationship <span className="text-red-500">*</span></label><input ref={primaryContactRelationshipRef} type="text" name="primaryContactRelationship" value={formData.primaryContactRelationship} onChange={handleInputChange} className={`border rounded px-3 py-2 w-full ${formErrors.primaryContactRelationship ? 'border-red-500' : ''}`} required/></div>
                  <div><label>Phone <span className="text-red-500">*</span></label><input ref={primaryContactPhoneRef} type="text" name="primaryContactPhone" value={formData.primaryContactPhone} onChange={handleInputChange} placeholder="09xxxxxxxxx" className={`border rounded px-3 py-2 w-full ${formErrors.primaryContactPhone ? 'border-red-500' : ''}`} maxLength={11} required/></div>
                  <div><label>Address</label><input type="text" name="primaryContactAddress" value={formData.primaryContactAddress} onChange={handleInputChange} className="border rounded px-3 py-2 w-full"/></div>
                  <div><label>Secondary Contact Name</label><input type="text" name="secondaryContactName" value={formData.secondaryContactName} onChange={handleInputChange} className="border rounded px-3 py-2 w-full"/></div>
                  <div><label>Relationship</label><input type="text" name="secondaryContactRelationship" value={formData.secondaryContactRelationship} onChange={handleInputChange} className="border rounded px-3 py-2 w-full"/></div>
                  <div><label>Phone</label><input type="text" name="secondaryContactPhone" value={formData.secondaryContactPhone} onChange={handleInputChange} className="border rounded px-3 py-2 w-full"/></div>
                  <div><label>Address</label><input type="text" name="secondaryContactAddress" value={formData.secondaryContactAddress} onChange={handleInputChange} className="border rounded px-3 py-2 w-full"/></div>
                </div>
              </div>



              {/* Buttons */}
              <div className="flex gap-2 mt-4">
                <button onClick={handleAddOrEdit} className="bg-[#8B3A3A] text-white px-6 py-2 rounded hover:bg-[#6D2E2E]">
                  {editingPatient ? "Save Changes" : "Add Patient"}
                </button>
                <button onClick={() => { setShowForm(false); setEditingPatient(null); setSelectedDateOfBirth(undefined); setFormErrors({}); }} className="border px-6 py-2 rounded hover:bg-gray-100">
                  Cancel
                </button>
              </div>
            </div>
            {/* FORM END */}
          </div>
        )}

        {/* Patient Cards */}
        {isLoading ? (
          <div className="text-center py-8">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-[#8B3A3A] mx-auto"></div>
            <p className="text-gray-600 mt-2">Loading patients...</p>
          </div>
        ) : filteredPatients.length === 0 ? (
          <div className="text-center py-12">
            <div className="text-gray-400 text-6xl mb-4"></div>
            <h3 className="text-xl font-semibold text-gray-600 mb-2">No patients found</h3>
            <p className="text-gray-500">
              {searchTerm || roleFilter !== "all"
                ? "Try adjusting your search or filters"
                : "Start by adding your first patient record"}
            </p>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredPatients.map((patient) => (
              <div
                key={patient.id}
                className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden hover:shadow-md transition-shadow"
              >
                {/* Card Header */}
                <div className="bg-[#8B3A3A] text-white p-4">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <h3 className="font-semibold text-lg leading-tight">
                        {getFullName(patient)}
                      </h3>
                      <p className="text-white/80 text-sm capitalize">
                        {patient.role.replace("_", " ")}
                      </p>
                    </div>
                    <div className="flex gap-1">
                      {canEditPatients && (
                        <button
                          onClick={() => handleEditClick(patient)}
                          className="p-1.5 hover:bg-white/20 rounded transition-colors"
                          title="Edit patient"
                        >
                          <Edit2 size={16} />
                        </button>
                      )}
                      {canManagePatients && (
                        <AlertDialog>
                          <AlertDialogTrigger asChild>
                            <button
                              className="p-1.5 hover:bg-red-500/20 rounded transition-colors"
                              title="Delete patient"
                            >
                              <X size={16} />
                            </button>
                          </AlertDialogTrigger>
                          <AlertDialogContent>
                            <AlertDialogHeader>
                              <AlertDialogTitle>Delete Patient</AlertDialogTitle>
                              <AlertDialogDescription>
                                Are you sure you want to delete {getFullName(patient)}? This action cannot be undone.
                              </AlertDialogDescription>
                            </AlertDialogHeader>
                            <AlertDialogFooter>
                              <AlertDialogCancel>Cancel</AlertDialogCancel>
                              <AlertDialogAction onClick={() => handleDelete(patient.id)} className="bg-red-600 hover:bg-red-700">
                                Delete
                              </AlertDialogAction>
                            </AlertDialogFooter>
                          </AlertDialogContent>
                        </AlertDialog>
                      )}
                    </div>
                  </div>
                </div>

                {/* Card Content */}
                <div className="p-4 space-y-3">
                  {/* Basic Info */}
                  <div className="grid grid-cols-2 gap-3 text-sm">
                    <div>
                      <span className="text-gray-500 block">ID Number</span>
                      <span className="font-medium">{patient.idNumber}</span>
                    </div>
                    <div>
                      <span className="text-gray-500 block">Gender</span>
                      <span className="font-medium">{patient.gender}</span>
                    </div>
                    <div>
                      <span className="text-gray-500 block">Phone</span>
                      <span className="font-medium">{patient.phone}</span>
                    </div>
                    <div>
                      <span className="text-gray-500 block">Birth Date</span>
                      <span className="font-medium">
                        {new Date(patient.dateOfBirth).toLocaleDateString('en-US', {
                          year: 'numeric',
                          month: 'long',
                          day: 'numeric'
                        })}
                      </span>
                    </div>
                  </div>

                  {/* Address */}
                  <div>
                    <span className="text-gray-500 block text-sm">Address</span>
                    <span className="text-sm">{patient.address || '-'}</span>
                  </div>

                  {/* Role-specific information */}
                  {patient.role === "student" && (
                    <div className="bg-blue-50 rounded-lg p-3 space-y-2">
                      <h4 className="font-medium text-blue-900 text-sm">Student Details</h4>
                      <div className="grid grid-cols-2 gap-2 text-xs">
                        <div>
                          <span className="text-blue-700 block">Program</span>
                          <span className="font-medium">{patient.program}</span>
                        </div>
                        <div>
                          <span className="text-blue-700 block">Course</span>
                          <span className="font-medium">{patient.course}</span>
                        </div>
                        <div>
                          <span className="text-blue-700 block">Year</span>
                          <span className="font-medium">{patient.yearLevel}</span>
                        </div>
                        <div>
                          <span className="text-blue-700 block">Block</span>
                          <span className="font-medium">{patient.block}</span>
                        </div>
                      </div>
                    </div>
                  )}

                  {patient.role === "teaching_staff" && (
                    <div className="bg-green-50 rounded-lg p-3">
                      <h4 className="font-medium text-green-900 text-sm">Teaching Staff Details</h4>
                      <div className="text-xs">
                        <span className="text-green-700 block">Department</span>
                        <span className="font-medium">{patient.department || '-'}</span>
                      </div>
                    </div>
                  )}

                  {patient.role === "non_teaching_staff" && (
                    <div className="bg-yellow-50 rounded-lg p-3">
                      <h4 className="font-medium text-yellow-900 text-sm">Non-Teaching Staff Details</h4>
                      <div className="text-xs">
                        <span className="text-yellow-700 block">Category</span>
                        <span className="font-medium">{patient.staffCategory || '-'}</span>
                      </div>
                    </div>
                  )}

                  {/* Medical Info Toggle */}
                  <div className="border-t pt-3">
                    <button
                      onClick={() => setExpandedId(expandedId === patient.id ? null : patient.id)}
                      className="text-[#8B3A3A] hover:text-[#6D2E2E] text-sm font-medium flex items-center gap-1"
                    >
                      {expandedId === patient.id ? "Hide" : "Show"} Medical Info
                    </button>

                    {expandedId === patient.id && (
                      <div className="mt-3 space-y-2 text-xs">
                        <div>
                          <span className="text-gray-500 block">Past Illnesses</span>
                          <span>{patient.pastIllnesses || '-'}</span>
                        </div>
                        <div>
                          <span className="text-gray-500 block">Surgeries</span>
                          <span>{patient.surgeries || '-'}</span>
                        </div>
                        <div>
                          <span className="text-gray-500 block">Current Medication</span>
                          <span>{patient.currentMedication || '-'}</span>
                        </div>
                        <div>
                          <span className="text-gray-500 block">Allergies</span>
                          <span>{patient.allergies || '-'}</span>
                        </div>
                        <div>
                          <span className="text-gray-500 block">Medical Notes</span>
                          <span>{patient.medicalNotes || '-'}</span>
                        </div>
                      </div>
                    )}
                  </div>

                  {/* Attachments */}
                  {patient.attachments && (
                    <div className="flex items-center gap-2 text-sm text-gray-600">
                      <Paperclip size={14} />
                      <span>Attachment available</span>
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </LayoutWrapper>
  )
}
